// Variables
$white  : #fff;
$black  : #000;

// common classes
.section{
    padding: 100px 0;
}

// hero area
.hero-bg{
    background: url(../images/header-bg.png);
    background-size: cover;
    background-position: center center;
    height: calc(70vh);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    &:before{
        position: absolute;
        content: '';
        height: 100%;
        width: 100%;
        background: rgba($color: $black, $alpha: .5);
    }
    .hero-content{
        h1 {
            font-size:50px;
            font-weight: 700;
        }
        p {
            font-size:20px;
        }
        h1,p{
            color: $white; 
        }
    }
}

//counter
.counter{
    background: $black;
    .counter-block{
        h1,p{
            color: $white;
        }
        h1{
            font-size: 5rem;
            font-weight: 600;
        }
        p{
            font-weight: 300;
            text-transform: uppercase;
        }
    }
}

.ui-components{
    .component-menu{
        position: sticky;
        top: 30px;
        max-height: 80vh;
        overflow-y: auto;
        padding: 10px 30px;
        box-shadow: 0px 0px 20px 0px rgba($color: $black, $alpha: .12);
        border-radius: 4px;
        ul{
            padding: 0;
            li{
                list-style: none;
                a{
                    text-decoration: none;
                    padding: 12px 0;
                    border-bottom: 1px solid #f8f8f8;
                    display: block;
                    color: $black;
                    font-weight: 600;
                    font-size: 14px;
                }
                &:last-child{
                    a{
                        border-bottom: none;
                    }
                }
            }
        }
    }
}

.components-block{
    padding-bottom: 100px;
    .title {
        font-size:18px;
        font-weight: 600;
        border-bottom:1px solid #dedede;
        padding-bottom: 10px;
        margin-bottom:30px;
        text-transform: uppercase;
    }
    .title-sm{
        margin-bottom: 20px;
    }
}